/* General container styling */

.faq-title {
    font-size: 2.5em;
    font-weight: bold;
    color: #ffffff;
    margin-bottom: 20px;
    text-transform: uppercase;
  }
.faq-container {
    width: 60%;
    margin: 50px auto;
    padding: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* FAQ item styling */
  .faq-item {
    margin-bottom: 20px;
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;
  }
  
  .faq-item:hover {
    transform: scale(1.02);
  }
  
  /* Question styling */
  .faq-question {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    font-size: 1.3em;
    font-weight: 600;
    color: #333;
  }
  
  .faq-question:hover {
    color: #007bff;
  }
  
  /* Arrow symbol */
  .arrow {
    font-size: 1.5em;
    color: #888;
    transition: transform 0.3s;
  }
  
  /* Rotate arrow when the question is open */
  .arrow:hover {
    color: #007bff;
  }
  
  .faq-answer {
    margin-top: 15px;
    font-size: 1.1em;
    color: #555;
    line-height: 1.6;
  }
  
/* Smooth transition for showing/hiding the FAQ answer */
.faq-answer {
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: max-height 0.6s ease, opacity 0.6s ease;
  }
  
  .faq-answer.open {
    max-height: 500px; /* You can adjust this based on the expected max content height */
    opacity: 1;
  }
  
  
  /* Form styling */
  .faq-form {
    margin-top: 40px;
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .faq-form h3 {
    font-size: 1.5em;
    color: #333;
    margin-bottom: 20px;
  }
  
  .question-input {
    width: 100%;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 1em;
    margin-bottom: 20px;
    resize: none;
    box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.05);
    transition: border-color 0.3s ease;
  }
  
  .question-input:focus {
    outline: none;
    border-color: #007bff;
  }
  
  /* Button styling */
  .submit-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 12px 25px;
    font-size: 1.1em;
    font-weight: 600;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 2px 10px rgba(0, 123, 255, 0.3);
    transition: all 0.3s ease-in-out;
  }
  
  .submit-button:hover {
    background-color: #0056b3;
    box-shadow: 0 4px 12px rgba(0, 123, 255, 0.4);
  }
  
  .submit-button:focus {
    outline: none;
  }
  
  /* FAQ container and form responsive styling */
  @media (max-width: 768px) {
    .faq-container {
      width: 90%;
    }
  
    .faq-form {
      padding: 20px;
    }
  
    .faq-item {
      padding: 15px;
    }
  }
  