.body{
    background: linear-gradient(180deg, #415A77, black);
    border-radius: 15px;
    padding: 40px;
    max-width: 800px;
    margin: 40px auto;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  .about-container {
      background: white;
      border-radius: 15px;
      padding: 40px;
      max-width: 800px;
      margin: 40px auto;
      box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
      text-align: center;
    }
  
  .icon {
      width: 60px;
      height: auto;
      }
    .about-title {
      font-size: 2.5em;
      font-weight: bold;
      color: #333;
      margin-bottom: 20px;
      text-transform: uppercase;
    }
    
    .about-text {
      font-size: 1.2em;
      color: #555;
      line-height: 1.8;
      margin-bottom: 20px;
    }
    
    .about-list {
      list-style: none;
      padding: 0;
      margin-bottom: 20px;
    }
    
    .about-list li {
      font-size: 1.1em;
      color: #444;
      background: #e8f4f8;
      border-radius: 10px;
      padding: 15px;
      margin: 10px 0;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
    }
    
    .about-list li strong {
      color: #333;
    }
    
    @media (max-width: 768px) {
      .about-container {
        padding: 20px;
      }
    
      .about-title {
        font-size: 2em;
      }
    
      .about-text {
        font-size: 1em;
      }
    
      .about-list li {
        font-size: 1em;
      }
  
    }
    