/* LoginPage.module.css */
.body {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-image: linear-gradient(180deg, #415A77, black);
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .container {
    width: 1000px;
    background: #fff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    display:flex;
    flex-direction: row;
    border-radius: 8px;
    justify-content: center;
    overflow: hidden;
  }
  
  .welcomeSection {
    width: 50%;
    background: linear-gradient(45deg, black, #415A77);
    color: white;
    padding: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .logo {
    display: flex;
    align-items: center;
  }
  
  .logo img {
    width: 75px;
    margin-right: 10px;
    border-radius: 50%;
  }
  
  .companyName {
    font-size: 24px;
    font-weight: bold;
    color: white;
  }
  
  .welcomeSection h1 {
    margin: 20px 0;
    font-size: 32px;
  }
  
  .welcomeSection p {
    font-size: 20px;
  }
  
  .loginSection {
    width: 50%;
    padding: 40px;
  }
  
  .inputGroup {
    margin-bottom: 20px;
  }
  
  .inputGroup label {
    display: block;
    margin-bottom: 5px;
    font-size: 14px;
    color: #333;
  }
  
  .inputGroup input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .forgotPassword {
    color: #0072ff;
    text-decoration: none;
    font-size: 14px;
  }
  
  .actions {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .btn {
    padding: 10px 30px;
    font-size: 16px;
    border: 2px solid #0072ff;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .loginBtn {
    background: #0072ff;
    color: white;
  }
  
  .signupBtn {
    background: white;
    color: #0072ff;
  }
  
  .btn:hover {
    background: #005bb5;
    color: white;
  }
  
  .signupBtn:hover {
    background: #f3f7fa;
    color: #005bb5;
  }
  
  .googleSignin {
    margin-bottom: 20px;
  }
  
  .googleBtn {
    background: #ffffff;
    color: #333;
    border: 1px solid #ddd;
    display: flex;
    align-items: center;
    padding: 10px;
    width: 100%;
    justify-content: center;
    cursor: pointer;
  }
  
  .googleBtn img {
    width: 20px;
    margin-right: 10px;
  }
  
  .googleBtn:hover {
    background: #f3f7fa;
    color: black;
  }
  
  .googleBtn:active {
    background: #e0e4e7;
    color: black;
  }
  
  .socialMedia {
    text-align: center;
  }
  
  .socialMedia span {
    display: block;
    margin-bottom: 10px;
    color: #777;
  }
  
  .socialMedia a img {
    width: 24px;
    margin: 0 20px 10px;
  }